import TradeFinanceService from '@/services/trade/TradeFinanceService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let ExportLcBillDueTableMixin = {
  mixins: [ListLayoutMixin, LoadingMixin],
  data: function () {
    return {
      exportLcTableData: [],
      exportLcTableContainer: 'loading-container',
      exportLcTableBusy: false,
      exportLcTableTotalRows: 0,
      exportLcTableCurrentPage: 1,
      exportLcTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('LC_NUMBER'),
        field: 'lcNumber',
        format: 'Number'
      }, {
        headerName: this.$t('LC_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('APPLICANT_NAME'),
        field: 'beneficiaryName'
      }, {
        headerName: this.$t('DUE_DATE'),
        field: 'dueDate',
        format: 'Date'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('AMOUNT'),
        field: 'billAmount',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    exportLcTableNoDataText: function () {
      return this.exportLcTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    exportLcTableTotalPages: function () {
      return Math.ceil(this.exportLcTableTotalRows / this.exportLcTableCurrentPage)
    },
    exportLcTablePagingInfo: function () {
      return this.pagingInfo(this.exportLcTableCurrentPage, this.exportLcTablePerPage, this.exportLcTableTotalRows)
    }
  },
  methods: {
    loadLcTableData (loadingContainerId) {
      let self = this
      this.exportLcTableBusy = true
      this.exportLcTableContainer = loadingContainerId || this.exportLcTableContainer
      this.startLoading(this.exportLcTableContainer)
      TradeFinanceService.dueExportLcBills({
      }).then(function (response) {
        self.stopLoading(self.exportLcTableContainer)
        self.exportLcTableBusy = false
        self.exportLcTablePerPage = response.data.totalRows
        self.exportLcTableTotalRows = response.data.totalRows
        self.exportLcTableData = response.data.billList || []
      }).catch(function (error) {
        self.stopLoading(self.exportLcTableContainer)
        self.exportLcTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLcTable () {
      this.loadLcTableData()
    }
  },
  watch: {
    exportLcTableCurrentPage: function () {
      this.refreshLcTable()
    }
  }
}

export default ExportLcBillDueTableMixin
