<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`ExportLcBillDue${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import ExportLcBillDueListLayout from '@/views/trade/export-lc/export-lc-bill-due/export-lc-bill-due-browse/ExportLcBillDueListLayout'
import ExportLcBillDueCardLayout from '@/views/trade/export-lc/export-lc-bill-due/export-lc-bill-due-browse/ExportLcBillDueCardLayout'
import ExportLcBillDuePivotLayout from '@/views/trade/export-lc/export-lc-bill-due/export-lc-bill-due-browse/ExportLcBillDuePivotLayout'
import ExportLcBillDueTableMixin from '@/mixins/trade/export-lc/ExportLcBillDueTableMixin'

export default {
  name: 'export-lc-bill-due-browse',
  mixins: [ExportLcBillDueTableMixin],
  components: {
    ExportLcBillDueListLayout,
    ExportLcBillDueCardLayout,
    ExportLcBillDuePivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadLcTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
